import React, { useState } from 'react'
import { NavDropdown } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'
import { AVATAR_PATH } from '../../config'
// import { useHistory } from 'react-router-dom'
import { Icon } from '@iconify/react'

const EmmaProfile = (props) => {
  const [showProfile, setShowProfile] = useState(false)
  const {
    user,
    setUser,
    setShowLogin,
    setShowSignUp,
    styleName,
    // isActive,
    setIsActive,
    navAvatar,
  } = props
  // const history = useHistory()
  // 
  const SignOut = () => {
    // localStorage 
    localStorage.removeItem('member')
    localStorage.removeItem('token')
    // state 
    setUser({
      login: false,
      member_sid: '', //ID
      avatar: '', //
      nickname: '', //
    })
    // history.push('/')
  }
  return (
    <>
      {user.login ? (
        <>
          <NavDropdown
            title={
              <div className="emma-profile-img">
                <img
                  src={
                    navAvatar
                      ? AVATAR_PATH + navAvatar
                      : AVATAR_PATH + 'default-avatar.jpg'
                  }
                  alt=""
                />
              </div>
            }
            id="collasible-nav-dropdown"
            className={styleName}
            alignRight
            show={showProfile}
            onMouseEnter={() => {
              setShowProfile(true)
            }}
            onMouseLeave={() => {
              setShowProfile(false)
            }}
            onClick={() => {
              setShowProfile(false)
            }}
          >
            <NavDropdown.Item className="emma-remove-link-style">
              {user.nickname}
            </NavDropdown.Item>
            <NavDropdown.Item
              as={NavLink}
              to={'/admin/user/profile/' + user.member_sid}
              onClick={() => setIsActive('')}
            >
              
            </NavDropdown.Item>
            <NavDropdown.Item
              as={NavLink}
              to={'/admin/user/recipient/' + user.member_sid}
              onClick={() => setIsActive('')}
            >
              
            </NavDropdown.Item>
            <NavDropdown.Item
              as={NavLink}
              to={'/admin/user/order/' + user.member_sid}
              onClick={() => setIsActive('')}
            >
              
            </NavDropdown.Item>
            <NavDropdown.Item
              as={NavLink}
              to={'/admin/user/favorite/' + user.member_sid}
              onClick={() => setIsActive('')}
            >
              
            </NavDropdown.Item>
            <NavDropdown.Item
              as={NavLink}
              to={'/admin/user/meetUp/' + user.member_sid}
              onClick={() => setIsActive('')}
            >
              Meet Up
            </NavDropdown.Item>
            <NavDropdown.Item
              as={NavLink}
              to={'/admin/user/rank/' + user.member_sid}
              onClick={() => setIsActive('')}
            >
              
            </NavDropdown.Item>
            <NavDropdown.Divider />

            <NavDropdown.Item
              onClick={() => {
                SignOut()
              }}
            >
              
            </NavDropdown.Item>
          </NavDropdown>
        </>
      ) : (
        <>
          <NavDropdown
            title={<Icon icon="iconoir:profile-circled" width="23" />}
            id="collasible-nav-dropdown"
            className={styleName}
            alignRight
            show={showProfile}
            onMouseEnter={() => {
              setShowProfile(true)
            }}
            onMouseLeave={() => {
              setShowProfile(false)
            }}
          >
            <NavDropdown.Item onClick={() => setShowSignUp(true)}>
              
            </NavDropdown.Item>
            <NavDropdown.Item onClick={() => setShowLogin(true)}>
              
            </NavDropdown.Item>
          </NavDropdown>
        </>
      )}
    </>
  )
}

export default EmmaProfile
